.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  background: white;
  opacity: 0;
  box-shadow: 2px 2px 50px rgba(128, 128, 128, 0.507);
  overflow: scroll;
  h2 {
    padding: 2rem;
  }
}

.library-song {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 1rem 2rem 1rem 2rem;
  transition: backgrounnd 0.5s ease;
  img {
    width: 30%;
    border-radius: 10%;
  }
  &:hover {
    background: rgba(161, 139, 221, 0.582);
  }
  .song-description {
    padding-left: 1rem;
    h3 {
      font-size: 1rem;
    }
    h4 {
      font-size: 0.7rem;
    }
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(82, 78, 78, 0.5) transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(110, 107, 107, 0.5);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: rgba(224, 222, 102, 0.877);
}

.active-library {
  transform: translateX(0);
  opacity: 1;
}

@media screen and(max-width: 768px) {
  .library {
    width: 100%;
  }
}
